@use 'variables' as *; // Make all variables from _variables.scss available in this file.

.container {
 max-width: $content-width + $sidebar-width + ($spacing-unit * 4);
 margin: 0 auto;
 padding: 0 $spacing-unit;
}

body.nav-open {
 overflow: hidden;
}

.site-header {
 margin: $spacing-unit 0;
 padding: $spacing-unit;
 background-color: var(--color-surface);
 border-radius: $border-radius-large;
 border: 1px solid var(--color-border);
 box-shadow: $box-shadow;
 transition: background-color 0.3s ease, border-color 0.3s ease;

 .header-content { display: flex; justify-content: space-between; align-items: center; }
 .site-branding { display: flex; align-items: center; gap: $spacing-unit; }
 .profile-image { width: 50px; height: 50px; border-radius: 50%; border: 2px solid var(--color-border); }
 .site-titles {
 .site-title { margin: 0; font-size: 1.3rem; a { color: var(--color-heading); } }
 .site-description { margin: 0; font-size: 0.85rem; color: var(--color-text-secondary); }
 }
}


.post-latest .post-title a {
 font-weight: 700;
 color: inherit;
 &:hover { color: var(--color-accent); }
}

.menu-toggle-button {
 display: flex; flex-direction: column; justify-content: space-around;
 width: 30px; height: 30px; background: transparent; border: none; cursor: pointer; padding: 0;
 z-index: 1001;
 .bar { width: 100%; height: 3px; background-color: var(--color-heading); border-radius: 3px; transition: all 0.3s ease-in-out; }
 &.is-active .bar:nth-child(1) { transform: translateY(8px) rotate(45deg); }
 &.is-active .bar:nth-child(2) { opacity: 0; }
 &.is-active .bar:nth-child(3) { transform: translateY(-8px) rotate(-45deg); }
}

.site-nav {
 position: fixed; top: 0; right: 0; width: 100%; height: 100%;
 background-color: rgba(17, 25, 33, 0.98);
 backdrop-filter: blur(5px);
 display: flex; flex-direction: column; align-items: center; justify-content: center;
 transform: translateX(100%); transition: transform 0.4s cubic-bezier(0.23, 1, 0.32, 1);
 z-index: 1000;
 a {
 color: var(--color-background); font-size: 1.75rem; font-weight: 700; margin: $spacing-unit 0;
 opacity: 0; transform: translateY(20px); transition: opacity 0.3s ease, transform 0.3s ease;
 }
}

body.nav-open .site-nav {
 transform: translateX(0);
 a { opacity: 1; transform: translateY(0);
 @for $i from 1 through 5 { &:nth-child(#{$i}) { transition-delay: #{0.1 + $i * 0.05}s; } }
 }
}

.main-content-wrapper {
 display: flex; flex-direction: column; gap: $spacing-unit * 2; margin-top: $spacing-unit;
}

.content-area, .sidebar-section {
 background: var(--color-surface); border-radius: $border-radius-large;
 border: 1px solid var(--color-border); box-shadow: $box-shadow;
 transition: background-color 0.3s ease, border-color 0.3s ease;
}
.content-area { padding: $spacing-unit * 2; }


.post-meta .meta-item + .meta-item {
 margin-inline-start: 1.25rem;
}

.sidebar {
 width: 100%; display: flex; flex-direction: column; gap: $spacing-unit * 2;
}
.sidebar-section {
 padding: $spacing-unit * 1.25;
 h4 {
 margin-top: 0; margin-bottom: $spacing-unit; padding-bottom: $spacing-unit * 0.5;
 border-bottom: 1px solid var(--color-border); font-size: 1.1rem;
 a { color: inherit; &:hover { text-decoration: none; color: var(--color-accent); } }
 }
 ul { list-style: none; padding: 0; margin: 0; }

 ul.custom-links-list li a, .recent-posts-list li a {
 position: relative; display: inline-block; padding: $spacing-unit * 0.25 0;
 &:hover { text-decoration: none; color: var(--color-accent); &::after { width: 100%; } }
 &::after {
 content: ''; position: absolute; bottom: 0; right: 0; width: 0; height: 1px;
 background-color: var(--color-accent); transition: width 0.3s ease;
 }
 }
 .tags-list, .categories-list-flex {
 display: flex; flex-wrap: wrap; gap: $spacing-unit * 0.6;
 .tag-item, .category-item-badge {
 font-size: 0.8rem; background-color: var(--color-background); color: var(--color-text-primary);
 padding: $spacing-unit * 0.3 $spacing-unit * 0.75; border-radius: $border-radius;
 border: 1px solid var(--color-border); font-weight: 500; transition: $transition-base;
 &:hover {
 background-color: var(--color-accent); border-color: var(--color-accent); color: var(--color-surface);
 text-decoration: none; transform: translateY(-2px); box-shadow: $box-shadow-sm;
 }
 }
 }
}

.post-list {
 padding: 0;
 margin: 0;
 list-style: none;
}

.post-summary {
 padding: $spacing-unit * 1.5; border-radius: $border-radius; border: 1px solid var(--color-border);
 background-color: var(--color-background); transition: $transition-base;
 display: flex; flex-direction: column; gap: $spacing-unit;
 &:not(:last-child) { margin-bottom: $spacing-unit * 1.5; }
 &:hover {
 border-color: var(--color-text-secondary); transform: translateY(-2px); box-shadow: $box-shadow-sm;
 }
 .post-thumbnail {
 width: 100%; height: 200px;
 img { width: 100%; height: 100%; object-fit: cover; border-radius: $border-radius-small; }
 }
 .read-more {
 position: relative; display: inline-block; font-weight: 700;
 &:hover { text-decoration: none; color: var(--color-accent-hover); &::after { width: 100%; } }
 &::after {
 content: ''; position: absolute; bottom: -1px; right: 0; width: 0; height: 1.5px;
 background-color: var(--color-accent-hover); transition: width 0.3s ease;
 }
 }
}

.post-featured-image {
 margin-bottom: 1.5rem;
}

.site-footer {
 padding: $spacing-unit * 3 0; margin-top: $spacing-unit * 2; text-align: center;
 font-size: 0.9rem; color: var(--color-text-secondary);
}

// Reading Progress Bar
#reading-progress-bar {
 position: fixed; top: 0; right: 0;
 height: 4px; background-color: var(--color-accent);
 width: 0%; z-index: 2000;
 transition: width 0.1s linear;
}

// Dark Mode Toggle Button
#theme-toggle {
 position: fixed; bottom: 20px; left: 20px; z-index: 1000;
 width: 45px; height: 45px;
 background-color: var(--color-surface);
 color: var(--color-text-primary);
 border: 1px solid var(--color-border);
 border-radius: 50%;
 display: flex; align-items: center; justify-content: center;
 cursor: pointer; box-shadow: $box-shadow;
 font-size: 1.2rem;
 transition: all 0.3s ease;
 &:hover {
 transform: translateY(-3px);
 background-color: var(--color-accent);
 color: var(--color-surface);
 border-color: var(--color-accent);
 }
}

// Copy Code Button
.copy-code-button {
 position: absolute; top: 10px; right: 10px;
 background-color: #334155;
 color: #cbd5e1;
 border: 1px solid #475569;
 border-radius: $border-radius-small;
 padding: 4px 8px;
 font-size: 0.8rem;
 font-family: $font-primary;
 cursor: pointer;
 opacity: 0;
 transition: opacity 0.3s ease;
 .bi { margin-inline-end: 4px; }
 &.copied { background-color: #16A34A; border-color: #16A34A; color: #fff; }
}
div.highlight:hover .copy-code-button {
 opacity: 1;
}

.related-posts {
 margin-top: $spacing-unit * 3;
 h3 { margin-bottom: $spacing-unit; }
 ul { list-style: none; padding: 0; }
 li a { font-weight: 700; }
}

@media (min-width: $breakpoint-md) {
 .site-header { margin: $spacing-unit * 2 0; }
 .post-summary {
 flex-direction: row; gap: $spacing-unit * 1.5;
 .post-thumbnail { flex-shrink: 0; width: 150px; height: 100px; }
 }
}

@media (min-width: $breakpoint-lg) {
 .menu-toggle-button { display: none; }
 .site-nav {
 position: static; transform: none; flex-direction: row;
 width: auto; height: auto; background: none; backdrop-filter: none;
 z-index: auto; gap: $spacing-unit * 1.5;
 a {
 font-family: $font-primary; font-size: 0.95rem; color: var(--color-heading);
 font-weight: 500; margin: 0; opacity: 1; transform: none;
 position: relative; padding-bottom: $spacing-unit * 0.25;
 transition: color 0.3s ease;
 &:hover { color: var(--color-accent); text-decoration: none; &::after { width: 100%; } }
 &::after {
 content: ''; position: absolute; bottom: -2px; right: 0; width: 0;
 height: 2px; background-color: var(--color-accent);
 transition: width 0.3s ease; border-radius: 2px;
 }
 }
 }
 .main-content-wrapper {
 flex-direction: row-reverse; align-items: flex-start;
 }
 .content-area { flex: 1; min-width: 0; }
 .sidebar { width: $sidebar-width; flex-shrink: 0; }
}

// Header Search Form
// -----------------------------------------------------------------------------
.header-search-form {
 display: flex;
 align-items: center;
 position: relative;
 
 // Mobile styles (inside the overlay)
 width: 90%;
 max-width: 400px;
 margin: 0 auto $spacing-unit * 3 auto;
 order: -1; // Show the search bar at the top of the mobile menu

 input[type="search"] {
 width: 100%;
 height: 44px;
 border: 1px solid var(--color-border);
 background-color: var(--color-surface);
 color: var(--color-text-primary);
 border-radius: $border-radius;
 padding: 0 $spacing-unit * 3 0 $spacing-unit;
 font-family: $font-primary;
 font-size: 1rem;
 transition: $transition-base;
 -webkit-appearance: none; // Removes default iOS styling

 &:focus {
 border-color: var(--color-accent);
 background-color: var(--color-background);
 outline: none;
 box-shadow: 0 0 0 3px rgba(var(--color-accent), 0.2);
 }
 }

 button[type="submit"] {
 position: absolute;
 right: 0;
 top: 0;
 bottom: 0;
 border: none;
 background: transparent;
 color: var(--color-text-secondary);
 padding: 0 $spacing-unit;
 cursor: pointer;
 font-size: 1.1rem;
 transition: color 0.2s ease;

 &:hover {
 color: var(--color-accent);
 }
 }
}

// Desktop styles
@media (min-width: $breakpoint-lg) {
 .header-search-form {
 width: 100%;
 max-width: 240px;
 margin: 0;
 order: 0; // Reset order for desktop layout

 input[type="search"] {
 height: 38px;
 font-size: 0.9rem;
 background-color: var(--color-background);

 &:focus {
 background-color: var(--color-surface);
 }
 }
 }

 // Adjust nav links to make space for the search form
 .site-nav {
 gap: $spacing-unit * 1.25;
 flex-grow: 1;
 justify-content: flex-end;
 }
}