@use 'variables' as *; // Make all variables from _variables.scss available in this file.

*, *::before, *::after {
 box-sizing: border-box;
}

html {
 font-size: 16px;
 scroll-behavior: smooth;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}

body {
 font-family: $font-primary;
 line-height: 1.65;
 color: var(--color-text-primary);
 background-color: var(--color-background);
 margin: 0;
 padding: 0;
 direction: rtl;
 text-align: start;
 transition: background-color 0.3s ease, color 0.3s ease;
 overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
 margin-top: $spacing-unit * 2;
 margin-bottom: $spacing-unit * 1;
 font-family: $font-secondary;
 font-weight: 700;
 line-height: 1.3;
 color: var(--color-heading);
 letter-spacing: -0.5px;
}

h1 { font-size: 2.75rem; }
h2 { font-size: 2.25rem; }
h3 { font-size: 1.75rem; }
h4 { font-size: 1.25rem; }
h5 { font-size: 1.1rem; }
h6 { font-size: 1rem; }

p {
 margin-top: 0;
 margin-bottom: $spacing-unit * 1.25;
}

a {
 color: var(--color-accent);
 text-decoration: none;
 transition: $transition-base;
 font-weight: 500;

 &:hover {
 color: var(--color-accent-hover);
 }
}

.post-content a {
 position: relative;
 text-decoration: none;
 background-image: linear-gradient(currentColor, currentColor);
 background-position: 0% 100%;
 background-repeat: no-repeat;
 background-size: 0% 1px;
 transition: background-size .3s ease;

 &:hover, &:focus {
 background-size: 100% 1px;
 color: var(--color-accent-hover);
 }
}

img {
 max-width: 100%;
 height: auto;
 display: block;
 border-radius: $border-radius;
}

ul, ol {
 padding-inline-start: $spacing-unit * 1.5;
 margin-bottom: $spacing-unit;
}

li {
 margin-bottom: $spacing-unit * 0.5;
}

.highlight {
 position: relative;
 background: var(--color-code-background);
 color: var(--color-code-text);
 padding: $spacing-unit * 1.25;
 margin-bottom: $spacing-unit * 1.5;
 overflow-x: auto;
 border-radius: $border-radius;
 direction: ltr;
 text-align: left;
 box-shadow: $box-shadow-sm;

 pre {
 margin: 0;
 font-family: $font-code;
 font-size: 0.9rem;
 line-height: 1.6;
 }
}

blockquote {
 margin: $spacing-unit * 1.5 0;
 padding: $spacing-unit $spacing-unit * 1.5;
 padding-inline-start: $spacing-unit * 1.75;
 border-inline-start: 4px solid var(--color-border);
 background-color: var(--color-surface);
 color: var(--color-text-secondary);
 font-style: normal;
 font-size: 1.1rem;
 border-radius: 0 $border-radius $border-radius 0;

 p {
 margin: 0;
 }
}

hr {
 border: 0;
 height: 1px;
 background-color: var(--color-border);
 margin: $spacing-unit * 3 0;
}