@use 'variables' as *; // Make all variables from _variables.scss available in this file.

// This file contains custom styles for the Pagefind UI to match the blog's theme.

.pagefind-ui {
 --pagefind-ui-font: #{$font-primary};
 --pagefind-ui-primary: var(--color-heading);
 --pagefind-ui-text: var(--color-text-primary);
 --pagefind-ui-background: var(--color-surface);
 --pagefind-ui-border: var(--color-border);
 --pagefind-ui-tag: var(--color-accent);
 --pagefind-ui-border-width: 1px;
 --pagefind-ui-border-radius: #{$border-radius};
 --pagefind-ui-image-border-radius: #{$border-radius-small};
 --pagefind-ui-image-box-ratio: 1.5; // Aspect ratio for result images
}

// Styling for the search input on the main search page
.pagefind-ui__search-input {
 background-color: var(--color-background);
 border-color: var(--color-border);
 color: var(--color-text-primary);
 padding: $spacing-unit * 0.75;
 font-size: 1.1rem;
 box-shadow: none;
 transition: $transition-base;
 height: 50px;

 &:focus {
 border-color: var(--color-accent);
 box-shadow: 0 0 0 3px rgba(var(--color-accent), 0.2);
 outline: none;
 }
}

// General container for the results
.pagefind-ui__results-area {
 margin-top: $spacing-unit * 2;
}

// Styling for each individual result item
.pagefind-ui__result {
 background-color: var(--color-surface);
 border: 1px solid var(--color-border);
 border-radius: $border-radius-large;
 margin-bottom: $spacing-unit * 1.5;
 padding: $spacing-unit * 1.5;
 transition: $transition-base;
 box-shadow: $box-shadow-sm;

 &:hover {
 border-color: var(--color-accent);
 transform: translateY(-3px);
 box-shadow: $box-shadow;
 }
}

// Result title
.pagefind-ui__result-title a {
 color: var(--color-heading);
 font-family: $font-secondary;
 font-weight: 700;
 font-size: 1.5rem;
 text-decoration: none;

 &:hover {
 color: var(--color-accent);
 }
}

// Result excerpt
.pagefind-ui__result-excerpt {
 color: var(--color-text-secondary);
 font-size: 0.95rem;
 margin-top: $spacing-unit * 0.5;
}

// Result link URL
.pagefind-ui__result-link {
 font-size: 0.85rem;
 color: var(--color-accent);
 opacity: 0.8;
 margin-top: $spacing-unit;
 display: block;
}

// Remove default sub-results indentation
.pagefind-ui__result-sub-results {
 margin-inline-start: 0;
 padding-inline-start: $spacing-unit;
 border-inline-start: 2px solid var(--color-border);
 margin-top: $spacing-unit;
}

// Remove default list styling from the results container generated by our custom template
.pagefind-ui__results {
 list-style: none;
 padding: 0;
 margin: 0;
}